import "./styles.css";

var text = document.getElementById("app");

var btnPlus = document.getElementById("btnLarge");
var btnMinus = document.getElementById("btnSmall");

var textSize = 16;
btnPlus.addEventListener("click", () => {
  textSize += 16;
  text.style.fontSize = textSize + "px";
});

btnMinus.addEventListener("click", () => {
  textSize -= 16;
  text.style.fontSize = textSize + "px";
});
